import React from "react";
import Movie from "../componet/Movie";
import { useEffect } from "react";
import { useState } from "react";
import "./Home.css"

function Home() {
  const [loading, setLoading] = useState(true);
  const [movieList, setMovieList] = useState([]);
  const getMovies = async () => {
    const json = await (
      (await fetch("https://yts.mx/api/v2/list_movies.json?minimum_rating=9&sort_by=year")
      )
        .json());
    setMovieList(json.data.movies);
    setLoading(false);
  };

  useEffect(() => {
    getMovies();
  }, []);

  return (
    <div>
      {loading ? (
        <img src="https://i.imgur.com/QFJ7W2U.gif" className={"background"} alt={"Sorry we can't find any picture"}></img>
      ) : (
        <div>
          {movieList.map((movie) => (
            <Movie 
            key={movie.id}
            id={movie.id}
            mediumCoverImage={movie.medium_cover_image} 
            title={movie.title} summary={movie.summary} 
            genres={movie.genres} 
            />
          ))}
        </div>
      )}
    </div>
  );
}

export default Home;